























































































.appdetail {
  width: 100vw;
  height: 100vh;
  background: #f6f6f6;
  .detailTop {
    width: 100vw;
    height: 10vh;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
      width: 7vw;
      height: 7vw;
      position: absolute;
      top: 6.5vw;
      left: 3vw;
    }
    div {
      font-size: 6.5vw;
      color: white;
    }
  }
  .detailTabs {
    width: 100vw;
    height: 5vh;
    line-height: 5vh;
    display: flex;
    color: #6c6c6c;
    background: #f6f6f6;
    margin-bottom: 2vw;
    .TabsLeft,
    .TabsRight {
      flex: 1;
      font-size: 4.2vw;
      font-weight: 500;
      text-align: center;
      margin: 0 1.4vw;
      color: #6c6c6c;
    }
  }
  .detailTabsBom {
    color: black;
    font-weight: 900 !important;
    // border-bottom: 1px solid green;
    margin: 0 1.4vw;
    position: relative;
    &::after {
      content: "";
      width: 100%;
      height: 1%;
      bottom: 10%;
      left: 0;
      position: absolute;
      background: #3cc18c;
      border: 1px solid #97979741;
      border-radius: 1px;
    }
  }
  .detailContent {
    width: 100vw;
    // height: 85vh;

    box-sizing: border-box;
    // padding: 1vw 0 0 0;
    background-color: #f6f6f6;
  }
  .detailBom {
    width: 96vw;
    height: 10vh;
    position: fixed;
    left: 2vw;
    bottom: 0;
    background: url("../../../../../assets/phone/sign_up.png") no-repeat;
    background-size: 100% 100%;
    img {
      width: 100%;
      // height: 100%;
    }
  }
}
