
























































































































































































































































































.ContentLeft {
  width: 96vw;
  background-color: #f6f6f6;
  margin: 0 auto;
  height: 84vh;
  padding: 0.5vw;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    display: none;
  }

  .ItemBox {
    background-color: white;
    margin-bottom: 2vw;
    padding: 0 2vw;
    .appHomeTitBox {
      height: 6.55vw;
      line-height: 6.55vw;
      font-size: 4vw;
      // margin: 0vw 0 0 1vw;
      padding-top: 1vw;
      display: flex;
      justify-content: space-between;
      .titleName {
        font-size: 5vw;
        font-weight: 600;
        color: #484848;
        padding-left: 3vw;
        position: relative;
        margin-left: 1vw;

        &::before {
          content: " ";
          position: absolute;
          height: 70%;
          width: 2px;
          top: 15%;
          left: 0;
          background-color: #484848;
        }
      }
      .GreenName {
        font-size: 3.2vw;
        font-weight: bold;
        color: #008c8a;
        margin-right: 2vw;
      }
    }
    .mTop {
      margin-top: -1vw;
      .GreenName {
        margin-top: -1vw;
      }
    }

    .appHomeContent {
      width: 100%;
      margin: 2.6vw 0 0;
      display: flex;
      flex-wrap: wrap;
      .mapBox {
        width: 100%;
        height: 40vh;
        border: 2px solid #81c2bb;
        padding: 1vw;
      }
      .contentItem {
        width: 100%;
        display: flex;
        margin-bottom: 3.42vw;
        margin-left: 1vw;
        position: relative;
        box-sizing: border-box;
        .itemLabel {
          width: 19vw;
          height: 2.8vh;
          margin-right: 0.9vw;
          padding-left: 1.8vw;
          font-size: 3.8vw;
          font-weight: 600;
          padding-left: 3vw;
          color: #44aaa9;
          position: relative;

          &::before {
            content: " ";
            position: absolute;
            height: 70%;
            width: 2px;
            top: 15%;
            left: 0;
            background-color: #44aaa9;
          }
          // text-align-last: justify;
        }
        .redSpot {
          position: absolute;
          top: 2vw;
          font-size: 15vw;
          border-radius: 100%;
          background-color: red;
          width: 1.2vw;
          height: 1.2vw;
        }
        .itemValue {
          flex: 1;
          background: #f5f8fd;
          font-size: 3.2vw;
          font-family: PingFangSC, PingFangSC-Regular;
          font-weight: 400;
          text-align: left;
          color: #868786;
          padding-left: 1vw;
          pointer-events: none;
          .addDate {
            position: relative;
            &::after {
              display: block;
              content: "";
              border-width: 1vw;
              border-style: solid;
              border-color: #00eab3 transparent transparent transparent;
              position: absolute;
              right: 3%;
              top: calc(50% - 0.25vw);
            }
          }
          input {
            width: 100%;
            border: none;
            padding: 0.5vw 0;
            background: #f5f8fd;
          }
          input::placeholder {
            font-size: 2.66vw;
          }
        }
        .w77 {
          height: 4.4vw;
          line-height: 4.4vw;
          max-width: 77vw;
        }
        .w66 {
          height: 14.4vw;
          max-width: 77vw;
          overflow-y: auto;
          div {
            display: flex;
            justify-content: space-between;
            padding: 0 3vw 0 0.5vw;
            margin-top: 0.5vw;
          }

          &::-webkit-scrollbar {
            display: none;
          }
        }
      }
      .flex2 {
        width: calc(50% - 1vw);
      }
    }
  }
}
