  right: 24.33vw;
  z-index: 1001;
}
.mainToolBar.sjjc {
  top: 3vh;
  right: 3vw;
}
.barIcon {
  width: 2.13vw;
  height: 2.13vw;
  background: url("../../../assets/map/swdyximg/mapToolIcon.png") no-repeat;
  background-size: 100% 100%;
  cursor: pointer;
  position: absolute;
  right: 1.1vw;
  top: -0.2vh;
}
.barIcon.active {
  background: url("../../../assets/map/swdyximg/mapToolIconA.png") no-repeat;
  background-size: 100% 100%;
}
.bar {
  min-width: 33vw;
  min-height: 3.5vh;
  line-height: 3.5vh;
  transition: all 0.4s;
  margin-right: 4vw;
  .leftBox {
    min-width: 16.14vw;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    border: 1px solid #32c5ff;
    border-radius: 0.26vw;
    display: flex;

    .leftItem {
      flex: 1;
      font-size: 0.625vw;
      color: #fff;
      text-align: center;
      position: relative;
      cursor: pointer;
      .el-dropdown-link {
        color: #fff;
      }

      &:hover {
        color: #32c5ff;
      }

      .icon {
        display: inline-block;
        width: 0.8vw;
        height: 0.83vw;

        margin-right: 0.2vw;
        vertical-align: middle;
        margin-top: -0.1vh;
      }

      .dt {
        background: url("../../../assets/map/swdyximg/dt.png") no-repeat;
        background-size: 100% 100%;
      }

      .tc {
        background: url("../../../assets/map/swdyximg/tc.png") no-repeat;
        background-size: 100% 100%;
      }
      .clear {
        background: url("../../../assets/map/swdyximg/icon_map_clear.png") no-repeat;
        background-size: 100% 100%;
      }
      .qt {
        background: url("../../../assets/map/swdyximg/qt.png") no-repeat;
        background-size: 100% 100%;
      }

      .sq {
        background: url("../../../assets/map/swdyximg/sq.png") no-repeat;
        background-size: 100% 100%;
      }
      .export {
        background: url("../../../assets/map/common/icon_map_export.png")
          no-repeat;
        background-size: 100% 100%;
      }
      .location {
        background: url("../../../assets/map/common/icon_map_location.png")
          no-repeat;
        background-size: 100% 100%;
      }
      .measure {
        background: url("../../../assets/map/common/icon_map_measure.png")
          no-repeat;
        background-size: 100% 100%;
      }
      .mulit {
        background: url("../../../assets/map/common/icon_map_mulit.png")
          no-repeat;
        background-size: 100% 100%;
      }
      .plot {
        background: url("../../../assets/map/common/icon_map_plot.png")
          no-repeat;
        background-size: 100% 100%;
      }
      .search {
        background: url("../../../assets/map/common/search.png") no-repeat;
        background-size: 100% 100%;
      }
      &::after {
        content: "";
        width: 1px;
        height: 70%;
        position: absolute;
        top: 15%;
        right: 0;
        background: #32c5ff;
      }
    }
    .leftItem.cx {
      position: relative;
      &:hover {
        .cxDrawBox {
          display: block;
        }
      }
      .cxDrawBox {
        display: none;
        position: absolute;
        top: 100%;
        left: -25%;
        padding-top: 0.5vh;
        padding-bottom: 0.5vh;
        background: rgba(0, 0, 0, 0.4);
        width: 150%;
        .cxItem {
          font-size: 0.625vw;
          color: #1dd4ff;
          text-align: center;
          line-height: 2.5vh;
          &:hover {
            color: #ffaa00;
          }
        }
      }
    }

    .leftItem.n {
      &::after {
        width: 0;
      }
    }
  }

  .rightItem {
    width: 2vw;
    height: 3.8vh;
    margin-left: 0.57vw;
    background: url("../../../assets/map/swdyximg/dw.png") no-repeat;
    background-size: 100% 100%;
    cursor: pointer;
  }

  .dq {
    background: url("../../../assets/map/swdyximg/dq.png") no-repeat;
    background-size: 100% 100%;
  }
}
.bar.sjjc {
}
.toolBox.isOpen {
  right: 0.2vw;
}
