





































































.main {
  width: 9.27vw;
  height: 40vh;
  background-color: #01131296;
  border:1px solid #1dd4ff;
  border-radius:5px;
  box-shadow: 0px 0px 2px 2px #32C5FF96;
  background-size: 100% 100%;
  padding: 2px;
  transition: all 0.4s;
}
.main el-scrollbar{
  padding: 4px;
}
.main .feature{
  text-align: center;
  width: 100%;
}
.main .feature .title{
  font-size: 13px;
  background-color: #226078;
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  margin-top: 2px;
  padding: 4px;
}
.main .feature .content{
  text-align: left;
  background-color: #1dd4ff96;
  padding-left: 2px;
  padding-right: 2px;
}
.main .feature .content span{
  font-size: 12px;
}
