  position: relative;
  width: 100%;
  height: 100%;
}
.map {
  width: 100%;
  height: 100%;
  position: relative;
  .ol-popup {
    color: white;
    position: absolute;
    background: url("./img/populBg.png") no-repeat;
    background-size: 100% 100%;
    box-shadow: none;
    // padding: 15px;
    border-radius: 10px;
    bottom: 7px;
    left: -5vw;
    min-width: 220px;
  }
  .ol-popup.ol-popurMonitor {
    color: white;
    position: absolute;
    background: url("./img/mapPop.png") no-repeat;
    background-size: 100% 100%;
    box-shadow: none;
    width: 13.54vw;
    height: 12.5vh;
    // padding: 15px;
    border-radius: 10px;
    bottom: -4vh;
    left: -13.7vw;
    .ol-popup-closer {
      display: none;
    }
  }
  .ol-popup:after,
  .ol-popup:before {
    top: 100%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
    display: none;
  }
  .ol-popup:after {
    border-top-color: #0c5f6d7d;
    border-width: 10px;
    left: 48px;
    margin-left: -10px;
  }
  .ol-popup:before {
    border-top-color: #0c5f6d7d;
    border-width: 11px;
    left: 48px;
    margin-left: -11px;
  }
  .ol-popup-closer {
    text-decoration: none;
    width: 2vw;
    height: 2vw;
    background: url("./img/pclosebg.png") no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top: 5px;
    right: 5px;
    cursor: pointer;
  }
  .ol-popup-closer:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: 1vw;
    height: 1vw;
    background: url("./img/pclose.png") no-repeat;
    background-size: 100% 100%;
    content: "";
  }
  .ol-popup .column {
    margin: 2px;
    font-size: 14px;
  }
}
.layer_symbol {
  position: absolute;
  bottom: 7vh;
  transition: all 0.4s;
  z-index: 2;
  height: auto;
}
.layer_symbol2 {
  position: absolute;
  bottom: 11vh;
  transition: all 0.4s;
  z-index: 2;
  height: auto;
}
.map_tool_bar {
  position: absolute;
  top: 11.57vh;
  z-index: 3;
  transition: all 0.4s;
}
.map_layer {
  position: absolute;
  top: 16vh;
  z-index: 2;
  transition: all 0.4s;
}
.map_query_result {
  position: absolute;
  top: 20vh;
  z-index: 2;
  transition: all 0.4s;
}
.poiQuery {
  position: absolute;
  top: 11.57vh;
  z-index: 2;
  transition: all 0.4s;
  width: 15vw;
}
.buttonDraw {
  position: absolute;
  top: 10px;
  right: 10px;
}
.compass {
  width: 128px;
  height: 128px;
  position: absolute;
  top: 10px;
  left: 10px;
  background: url("./img/compass.png") no-repeat center;
}
