  width: 216px;
  height: 349px;
  background: url("../../../assets/map/swdyximg/layerbg.png") no-repeat;
  background-size: 100% 100%;
  padding: 18px;
  right: 44vw;
  transition: all 0.4s;
  .layersearch {
    width: 178px;
    height: 24px;
    border: 1px solid #1DD4FF;
    // background: url("../../../assets/screen1/layersearch.png") no-repeat;
    background-size: 100% 100%;
    position: relative;
    margin-bottom: 10px;
    input {
      border: none;
      width: 178px;
      height: 24px;
      background: none;
      outline: none;
      font-size: 12px;
      padding-left: 5px;
      color: #fff;
    }

    span {
      position: absolute;
      width: 14px;
      height: 14px;
      right: 5px;
      top: 5px;
      color: #fff;
      font-size: 14px;
      cursor: pointer;
    }
  }

  .layertree {
    font-size: 0;

    input[type="checkbox"] {
      -webkit-appearance: none;
      background: transparent;
      border: 1px solid #fff;
      border-radius: 3px;
      width: 14px;
      height: 14px;
      margin-top: -2px;
      box-sizing: border-box;
      position: relative;
      outline: none;
    }

    input[type="checkbox"]:checked {
      &::after {
        content: "✓";
        color: #333;
        font-size: 12px;
        display: inline-block;
        width: 14px;
        height: 14px;
        text-align: center;
        line-height: 14px;
        font-weight: bold;
        background-color: #1DD4FF;
        // border: 1px solid #fff;
        border-radius: 3px;
        position: absolute;
        top: -1px;
        left: -1px;
      }
    }

    .el-slider {
      display: inline-block;
      width: calc(100% - 50px);
      vertical-align: middle;
      margin-left: 10px;
    }

    .treeitem {
      transform: scale(0.8, 0.8);
      transform-origin: top left;
      width: 125%;
      height: 340px;

      .item1 {
        color: #1DD4FF;
        font-size: 14px;
        margin-bottom: 5px;
        line-height: 30px;

        .itemparent {
          position: relative;

          .lt {
            cursor: pointer;
            display: inline-block;
            width: 100%;
          }

          .btm {
            font-size: 12px;
            color: #fff;
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
          }
        }

        input {
          vertical-align: middle;
          margin-right: 5px;
        }

        .itemchild {
          color: #fff;
          font-size: 12px;
          margin-left: 10px;
          line-height: 28px;

          .itemtxt {
            cursor: pointer;
          }
        }

        .itemchild.nobor {
          border-bottom: none;
        }

        .layerorgin {
          border-bottom: 1px dashed #fff;
        }
      }
    }
  }
}

.layerbox.isOpen {
  right: 10.2vw;
}
