
















































































































































.mDialog {
  margin: 12px;
}

.mDialog .main {
  width: 100%;
  height: 100%;
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  padding: 4px;
}

.mDialog .main .left {
  width: 240px;
  height: 100%;
  background-color: #0E3255;
  padding: 8px;
  border-top-left-radius: 16px;
  border-bottom-left-radius: 16px;

  .mLayerName {
    color: #ffffff;
    margin: 8px;
  }
  .delete{
    margin: 8px;
    color: red;
  }
  .mSeletor {
    margin-top: 8px;
  }

  .mAddButton {
    margin-top: 8px;
    width: 100%;
    background-color: #32C5FF;
  }
}

.mDialog .main .right {
  width: 100%;
  flex-grow: 1;
  background-color: #ffffff;
  display: flex;
  display: -webkit-flex;
  flex-direction: row  ;
  flex-wrap:wrap;
  .map{
    min-width: 35vw;
    flex-grow:1;
  }
}

