





















































































.tweentContentBox {
  width: 100%;
  .detailContent {
    width: calc(100% - 4vw);
    margin: 0 auto;
    .TopH2 {
      height: 15%;
      padding: 3vw;
      color: #4a4a4a;
      box-sizing: border-box;
      background-color: #f9f9f9;
      .h2 {
        font-size: 4vw;
        font-weight: bold;
      }
      .topSmall {
        margin-top: 0.3vw;
        font-size: 4.2vw;
        font-weight: 600;
      }
    }
    .ContentText {
      height: calc(85% - 2vw);
      overflow-y: auto;
      padding: 3vw 3vw 3vw;
      // font-size: 3.3vw;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      background-color: #f9f9f9;
      margin: 1vw 0;

      &::-webkit-scrollbar {
        display: none;
      }
      .ContentImgBox {
        width: 100%;
        height: 20vh;
        display: flex;
        li:nth-child(1) {
          width: 60%;
          img {
            width: 100%;
            height: 100%;
          }
        }
        li:nth-child(2) {
          width: 40%;
          margin-left: 0.5vw;
          div {
            width: 100%;
            height: 50%;
            img {
              height: 100%;
              width: 100%;
            }
          }
          div:nth-child(2) {
            margin-top: 0.3vw;
          }
        }
      }
      .p2 {
        margin-top: 4vw;
      }
    }
  }
}
