





















































































































































































































































































.rowClassName {
  cursor: pointer;
}

.main1 {
  .pageBox {
    background: rgba(0, 0, 0, 0.4);
    .el-pagination {
      color: #fff;
      font-size: 0.73vw;
    }
    .el-pager li {
      font-size: 0.73vw;
    }
    .el-pagination button:disabled,
    .el-dialog,
    .el-pager li,
    .el-pagination .btn-next,
    .el-pagination .btn-prev,
    .el-pagination__jump,
    .el-input__inner {
      background: none;
      color: #fff;
    }
    .el-pagination__editor.el-input .el-input__inner {
      height: 2.4vh;
    }
  }
  .el-table {
    background: rgba(0, 0, 0, 0.2);
  }
  .el-table tr {
    background: rgba(0, 0, 0, 0.2);
  }
  .el-table--enable-row-hover .el-table__body tr:hover > td {
    background: rgba(0, 0, 0, 0.2);
  }
}
.query_box {
  .el-input__inner {
    background: rgba(0, 0, 0, 0.4);
    border: 1px solid #1cd3fd !important;
    border-top-left-radius: 4px !important;
    border-bottom-left-radius: 4px !important;
    color: #fff;
  }

  .el-input-group__append {
    .el-button {
      background: #1ed3fe;
    }
  }
}
.result-item{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
  .left{
    flex-grow: 1;
  }
  .mid{
    .mImage{
      width: 80px;
      height: 50px;
      border-radius: 4px;
      //border: #ffffff 0.1px solid;
    }
  }
  .right{
    line-height: 55px;
    i{
      margin-left: 4px;
    }
  }
}
