







































































































































.mDialog{
  display: flex;
  display: -webkit-flex;
  flex-direction: row;
}
.mDialog .left {
  width: 100%;
  flex-grow: 1;
  background-color: #ffffff;
  flex-wrap:wrap;
  padding:0.5em;
  border: 0.5em solid transparent;
  background: linear-gradient(white,white) padding-box,repeating-linear-gradient(-45deg, red 0, red 12.5%, transparent 0, transparent 25%, #58a 0, #58a 37.5%, transparent 0, transparent 50%) 0/5em 5em;
}
.mDialog .right{
  width: 300px;
  display: flex;
  display: -webkit-flex;
  flex-direction: column  ;
  margin-left: 16px;
  .top{
    flex-grow: 1;
    span{
      font-size: 14px;
    }
  }
  .bottom{
    display: flex;
    display: -webkit-flex;
    flex-direction: column  ;
    .mButton{
      margin: 4px;
      width: 250px;
    }
  }
}
