  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  .videoItem {
    height: 50vh;
    width: 90vw;
  }
}

.ContentRight {
  width: 100vw;
  .ItemBox {
    background-color: white;
    width: 95vw;
    margin: 0 auto;
    padding: 0 2vw;
    .appHomeTitBox {
      height: 6.55vw;
      line-height: 6.55vw;
      font-size: 4vw;
      margin: 0vw 0 0 1vw;
      padding-top: 3vw;
      display: flex;
      justify-content: space-between;
      .titleName {
        font-size: 5vw;
        font-weight: 600;
        color: #484848;
        padding-left: 3vw;
        position: relative;
        margin-left: 1vw;
        &::before {
          content: " ";
          position: absolute;
          height: 70%;
          width: 2px;
          top: 15%;
          left: 0;
          background-color: #484848;
        }
      }
    }
    .ContentBox {
      width: 100%;
      height: 45vh;
      padding-top: 3vw;
      overflow-y: auto;

      .AddImg {
        margin: 0 1vw;
        display: inline-block;
        position: relative;

        &::after {
          // content: attr(data-name);
          position: absolute;
          bottom: 0;
          left: 0;
          text-align: center;
          width: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 2vw;
          // transform: scale(0.8);
          color: #bf9b6b;
        }
        .item {
          height: 17vw;
          width: 17vw;
        }
        .img {
          background-size: cover !important;
        }
        .video {
          height: 8.5vw;
          width: 8.5vw;
          background: #00000026;
          padding: 4.25vw;
          .videoIcon {
            height: 100%;
            width: 100%;
            background-size: cover !important;
          }
        }
      }
    }
    .box2 {
      height: 30vh;
    }
  }
}
